<template>
  <div id="project">
    <h1></h1>
    <template v-for="item in items">
      <div class="row">
        <template v-for="li in item.con">
          <div class="col-2 center"><a class="circle-img"  v-bind:href = li.href><img v-bind:src = li.src /></a></div>
          <div class="col-4">
            <h2>
              <a v-bind:href = li.href>{{li.title}}</a>
            </h2>
            <p>{{li.content}}</p>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'project',
    data () {
      return {
        items:[
          {
            con:[
              {src:require('../assets/xcx.png'),title:'',href:'javascript:void(0)',content:'WXML+CSS+JSAPI()'},
              {src:require('../assets/gzh.png'),title:'',href:'javascript:void(0)',content:'MVC()'}
            ]
          },
          {
            con:[
              {src:require('../assets/pt.png'),title:'',href:'javascript:void(0)',content:'()'},
              {src:require('../assets/wz.png'),href:'http://www.posinda.com/',title:'',content:'()'}
            ]
          }
        ]
      }
    }
  }
</script>

<style>
  .row div{display: inline-block;}
  .circle-img {
    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #008a75;
    padding: 18px;
  }
</style>



